* {
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
}

body {
    font-family: StratosSkyeng, sans-serif;
    background: #004980;
}

.center {
    padding-left: calc(50% - 240px);
    padding-right: calc(50% - 240px);
}

.center-card {
    padding-left: calc(50% - 463px);
    padding-right: calc(50% - 463px);
}

.container {
    display: flex;
    align-items: center;
    padding-top: 158px;
    padding-bottom: 157px;
    min-height: 774px;
}

.box {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 48px;
    width: 480px;
    height: 459px;
    border-radius: 12px;
    background: #c2f5ff;
}

.title {
    width: 208px;
    height: 96px;
    color: #004980;
    font-size: 40px;
    font-style: normal;
    font-weight: 400;
    line-height: 48px;
    padding-top: 52px;
    text-align: center;
}

